<template lang='pug'>
  #app
    .form-group
      cf-cascader.cascader(
        v-model='results'
        :options='options'
        :fetch-data='fetchData'
        searchable
        :serverSideSearch='search'
      )
</template>

<script>
import 'bootstrap/dist/css/bootstrap.min.css'
import './custom-theme.scss'

export default {
  name: 'app',
  data () {
    return {
      results: [],
      options: [
        {
          value: 'internal',
          label: '国内',
          children: []
        },
        {
          value: 'external',
          label: '国外',
          children: []
        }
      ]
    }
  },
  methods: {
    search () {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve([
            {
              isSearchOption: true,
              label: '国内 / Coding',
              value: [
                {
                  value: 'internal',
                  label: '国内',
                  children: []
                },
                {
                  value: 'coding',
                  label: 'Coding',
                },
              ],
            },
            {
              isSearchOption: true,
              label: '国外 / Github',
              value: [
                {
                  value: 'external',
                  label: '国外',
                  children: []
                },
                {
                  value: 'gitlab',
                  label: 'Gitlab',
                },
              ],
            }
          ])
        }, 1000)
      })
    },
    fetchData (parent) {
      return new Promise(resolve => {
        setTimeout(() => {
          parent.children = parent.value === 'internal'
            ? [
                {
                  value: 'coding',
                  label: 'Coding',
                },
                {
                  value: 'tgit',
                  label: 'TGit',
                },
              ]
            : [
                {
                  value: 'github',
                  label: 'Github',
                },
                {
                  value: 'gitlab',
                  label: 'Gitlab',
                },
                {
                  value: 'bitbucket',
                  label: 'Bitbucket',
                },
              ]
          resolve()
        }, 1000)
      })
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>

